.mainWrapper{
    width: 100%;
}
header{
    width:100%;
    position: fixed;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.headBar{
    width: 100%;
    background: #1b1b1b;
    border-bottom: 1px solid rgba(235,235,235,0.1);
    padding: 0 131px;
}
.headBar ul{
    height: 31px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.headBar ul li{
    color: #fefefe;
    font-size: 0.7rem;
    opacity: 0.6;
    padding: 0 10px;
    border-right: 1px solid rgba(235,235,235,0.2);
    cursor: pointer;
}
.headBar ul li:last-child{
    padding-right: 0px;
    border-right: none;
}
.mainNav{
    width: 100%;
    height: 70px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 131px;
    background: #1b1b1b;
}
.mainNav h1 img{
    cursor: pointer;
    width: 100px;
}
.mainNav__category{
    width:690px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    height: 80px;
    border-bottom: none;
    z-index: 98;
    margin-left: 35px;
}
.mainNav__category li{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1.15rem;
    cursor: pointer;
    position: relative;
    margin: 0 18px;
}
.mainNav__category li:hover{
    opacity: 1;
}
.mainNav__category li.clicked{
    opacity: 1;
}
.mainNav__searchBtn{
    background: none;
    outline-style: none;
    border: none;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.mainNav__searchBtn img{
    width: 33px;
}
.mainNav__searchBtn span{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1rem;
}
.mainNav__catBtn{
    background: none;
    outline-style: none;
    border: none;
    display: flex;
    align-items: center;
    cursor: pointer;
}
.mainNav__catBtn img{
    width: 33px;
    opacity: 0.6;
}
.mainNav__catBtn:hover img{
    opacity: 1;
}
.mainNav__catBtn span{
    color: #fefefe;
    opacity: 0.6;
    font-size: 1rem;
}
.mainNav__catBtn:hover span{
    opacity: 1;
}
